.comming-soon {
    display: flex;
    align-items: center;
    justify-content: center;
}

.comming-soon lottie-player {
    width: 400px;
}

@media (max-width: 850px) {
    .comming-soon {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .comming-soon lottie-player {
        width: 100%;
    }
}

.open-game-div {
    pointer-events: none;
    opacity: 0;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.7);
    z-index: 9999;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 1s;
}

.open-game-div.open {
    pointer-events: all;
    opacity: 1;
}

.open-game-div .open-game-div-close-btn {
    position: absolute;
    top: 0;
    right: 0;
    background: #fff;
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0 0 0 50px;
    cursor: pointer;
}

.open-game-div .open-game-div-close-btn span {
    position: relative;
    top: -5px;
    left: 5px;
    color: #333C5D;
}

.open-game-div .open-game-div-heading {
    position: relative;
    width: 100%;
    border-radius: 8px 8px 0 0;
    background: #348DCB;
    text-align: center;
    height: 40px;
    border-bottom: 1px solid #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-right: 30px;
    z-index: 999;
}

@media (max-width: 1080px) {
    .open-game-div .open-game-div-heading {
        padding-right: 0;
    }
}

.open-game-div .open-game-div-heading h1 {
    position: relative;
    top: 5px;
    font-size: 0.9rem;
    color: #fff;
    text-align: center;
    text-transform: uppercase;
    font-weight: 400;
}

.open-game-div .open-game-div-heading div {
    pointer-events: all;
    opacity: 1;
    position: absolute;
    right: 0;
    top: 0;
    width: 30px;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

@media (max-width: 992px) {
    .open-game-div .open-game-div-heading div {
        pointer-events: none;
        opacity: 0;
    }
}


.open-game-div .open-game-div-heading div span {
    color: #fff;
}

.open-game-div .open-game-div-inner {
    width: 80%;
    max-height: 90vh;
    /* height: 90vh; */
    background: transparent;
    border-radius: 0 0 10px 10px;
    overflow-y: auto;
}

.game-section-card {
    position: relative;
    border-radius: 10px;
    cursor: pointer;
}

.game-section-card img {
    width: 100%;
    border-radius: 10px;
}

.game-section-card .game-name {
    position: absolute;
    bottom: -10px;
    left: 50%;
    transform: translateX(-50%);
    width: 90%;
    background: #348DCB;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
    z-index: 10;
    border-left: 1px solid #fff;
    border-right: 1px solid #fff;
    box-shadow: 0 10px 10px 0 rgba(0, 0, 0, 0.3);
}

.game-section-card .game-name-tag {
    position: absolute;
    bottom: 0px;
    width: 40px;
    height: 40px;
    background: #348DCB;
}

.game-section-card .game-name-tag.left {
    left: -7px;
    box-shadow: 10px 10px 10px 0 rgba(0, 0, 0, 0.3);
}

.game-section-card .game-name-tag.right {
    right: -7px;
    box-shadow: -10px 10px 10px 0 rgba(0, 0, 0, 0.3);
}

.game-section-card .game-name-tag::after {
    content: '';
    position: absolute;
    width: 0;
    height: 0;
    border-top: 20px solid transparent;
    border-bottom: 20px solid transparent;
}

.game-section-card .game-name-tag.left::after {
    border-left: 20px solid #fff;
}

.game-section-card .game-name-tag.right::after {
    border-right: 20px solid #fff;
    right: 0;
}

.game-section-card .game-name-tag::before {
    content: '';
    position: absolute;
    top: 0;
    width: 0;
    height: 0;
    border-bottom: 5px solid transparent;
    border-top: 10px solid transparent;
}

.game-section-card .game-name-tag.left::before {
    border-right: 12px solid #333C5D;
    right: 0;
}

.game-section-card .game-name-tag.right::before {
    border-left: 12px solid #333C5D;
    left: 0;
}

.game-section-card .game-name h1 {
    font-size: 1rem;
    position: relative;
    top: 3px;
    color: #fff;
    text-transform: uppercase;
    font-weight: 400;
}

.flip-game-outter-div {
    background: #333C5D;
    color: #fff;
}

.flip-game-inner-div {
    text-align: center;
    max-width: 850px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.flip-game-heading h1 {
    font-size: 1.5rem;
    padding-top: 10px;
}

.flip-game-heading h3 {
    font-size: 1.1rem;
    font-weight: 300;
}

.flip-game-heading p {
    font-size: 0.9rem;
    margin: 0;
}

/* Custom Style : Card Game */
button {
    margin-top: 10px;
    margin-bottom: 8px;
    background: none;
    border: 2px solid #fff;
    border-radius: 4px;
    color: #fff;
    font-weight: bold;
    cursor: pointer;
    font-size: 0.8rem;
    height: 40px;
    width: 120px;
    outline: none;
}

button:hover {
    background: #c23866;
    color: #fff;
    outline: none;
}

button span {
    position: relative;
    top: -1px;
}

.flip-game-card-grid {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
}

@media (max-width: 360px) {

    .flip-game-heading h1 {
        font-size: 1.3rem;
        padding-top: 10px;
    }

    .flip-game-heading h3 {
        font-size: 1rem;
        font-weight: 300;
    }

    .flip-game-heading p {
        font-size: 0.8rem;
    }

    .flip-game-card-grid {
        grid-template-columns: 1fr 1fr 1fr;
    }
}